import React from 'react'
import './style.css'
import {Link} from "react-router-dom";

function Home({movies,color,addtolike,search,setSearch}) {

    
    return (
        <>
            <div className="container">
                <div className="content">
                   <div className="header-main">
                        <div className="header-bar">
                            <div className="search-bar">
                                <input type="text" name="name" placeholder="search..."  
                                value={search} onChange={(e) => {setSearch(e.target.value)}}/>
                            </div>

                            <h3>Movies</h3>
                           
                            <Link to="/tvseries" style={{color:"white",textDecoration:"none"}}>
                            <h3>TV Series</h3>
                            </Link>
                           
                           <div className="userimg">
                               <img src='data:image/png;base64,'alt='imageurl' id='img2'/>
                           </div>
                        </div>
                        
                        <div className="section">
                            <img src='https://cdn.wegotthiscovered.com/wp-content/uploads/2018/04/Avengers-Infinity-War-poster-1.jpg'alt='imageurl' id='img1'/>
                        </div>

                        <div className="Movielist">
                            <h2>Movies</h2>
                            <div className="box-grid">
                            {movies.map((movie,idx) => (
                               <div className="box" key={idx}>
                                <img src={movie.Poster} alt='imageurl'/>
                                <h4>{movie.Title} 
                                <span className="icon" onClick={() => addtolike(movie)}><i class="bi bi-heart-fill"></i>
                                <svg xmlns="http://www.w3.org/2000/svg" width="23" height="32" fill={color} class="bi bi-heart-fill" viewBox="0 0 16 16">
                                     <path fill-rule="evenodd" d="M8 1.314C12.438-3.248 23.534 4.735 8 15-7.534 4.736 3.562-3.248 8 1.314z"/>
                                    </svg></span></h4>
                                    
                                </div>
                              
                            ))}
                            </div>
                        </div>
                    </div>
                </div>
            </div>

        </>
    )
}

export default Home

